<template>
  <div class="row m-0">
    <div class="col">
      <div class="row mt-3">
        <div class="col-12 mb-2">
          <div class="row m-0">
            <div class="col-4">
              <v-select
                v-model="casherResits.paymentmetoud"
                placeholder="  "
                label="name"
                v-validate="'required'"
                :options="paymentMethods"
                name="paymentmetoud"
              ></v-select>
              <span
                v-show="errors.has('paymentmetoud')"
                class="help is-danger"
              >{{ errors.first('paymentmetoud') }}</span>
            </div>
            <div class="col-4">
              <base-input
                v-model="casherResits.clintName"
                placeholder="  "
                v-validate="'required'"
                name="clintName"
                :valid="!errors.has('clintName')"
              ></base-input>
              <span class="help is-danger">{{ errors.first('clintName') }}</span>
            </div>
            <div class="col-4">
              <base-input
                v-model="casherResits.clintPhone"
                placeholder="   "
                type="number"
                v-validate="'required'"
                :valid="!errors.has(`phone`)"
                name="phone"
              ></base-input>
            </div>
          </div>
        </div>
        <div class="col-12">
          <div class="row m-0" v-for="(prod ,index) in casherproducts" :key="index">
            <div class="col-5 mb-5 mb-xl-0">
              <v-select
                placeholder=" "
                v-if="products.length>0"
                label="id"
                :options="products"
                :value="prod"
                @input="prod=>addproduct(prod, index)"
                :name="`code${index}`"
              ></v-select>
              <span class="help is-danger">{{ errors.first(`price${index}`) }}</span>
            </div>
            <div class="col-4">
              <base-input
                v-model="prod.quantity1"
                placeholder=" "
                type="number"
                v-validate="'required'"
                :valid="!errors.has(`price${index}`)"
                :name="`price${index}`"
              ></base-input>
              <span class="help is-danger">{{ errors.first(`price${index}`) }}</span>
            </div>
            <div class="col-3">
              <base-button @click="pushIncasherProudct()" type="primary" icon="ni ni-fat-add"></base-button>
              <base-button
                v-if="casherproducts.length>1"
                @click="deletefromcasherProudct(index)"
                type="primary"
                icon="ni ni-fat-delete"
              ></base-button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="col-4">
      <div class="row w-100">
        <div id="printMe" class="col-12 table-responsive">
          <div class="card-image">
            <figure class="image is-4by3">
              <img src="@/assets/image/logo.jpg" alt="Placeholder image" />
            </figure>
            <div class="row m-0 justify-content-center">
              total
              {{casherResits.totalprice}}
            </div>
          </div>
          <table class="table table-striped">
            <thead>
              <tr>
                <th v-for="col in tableColumns">{{col}}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="product in casherproducts ">
                <td>{{product.name}}</td>
                <td>{{product.outprice}}</td>

                <td>{{product.quantity1}}</td>
                <td>{{product.id}}</td>
              </tr>
            </tbody>
          </table>
          <div class="col-12">
            <div class="row m-0 justify-content-center">
              risit code
              <barcode
                v-if="casherResits.id"
                :value="casherResits.id"
                :options="{ lineColor: '#444e',height:'100px' }"
                tag="img"
              >{{casherResits.id}}</barcode>
            </div>
          </div>
        </div>
        <button
          v-if="printing"
          v-print="printObj"
          type="button"
          class="btn btn-outline-info mr-2 ml-2 mb-3"
        >
          <i class="fas fa-print"></i>
        </button>
        <button
          v-else
          @click="SubmitRisit()"
          type="button"
          class="btn btn-outline-info mr-2 ml-2 mb-3"
        > </button>
        <button
          @click="cleardata()"
          type="button"
          class="btn btn-outline-info mr-2 ml-2 mb-3"
        > </button>
        <!-- /.col -->
      </div>
    </div>
    <b-loading :is-full-page="true" :active.sync="isLoading" :can-cancel="true"></b-loading>
  </div>
</template>
<script>
import * as firebase from "firebase/app";
import * as moment from "moment";
import VueBarcode from "vue-barcode";

export default {
  components: {
    barcode: VueBarcode
  },
  data() {
    return {
      printObj: {
        id: "printMe"
      },
      printing: false,
      tableColumns: ["name", "price", "Quantity", "code"],

      isLoading: false,
      casherResits: {
        clintName: null,
        clintPhone: "",
        date: "",
        paid: "",
        paymentmetoud: "",
        rest: "",
        time: "",
        totalprice: "",
        type: "sell"
      },
      paymentMethod: null,
      paymentMethods: [
        { name: "", value: "visa" },
        { name: "", value: "cash" }
      ],
      products: [],
      casherproducts: [{}],
      product: {}
    };
  },
  methods: {
    addproduct(product, index) {
      this.casherproducts[index] = product;
    },
    deletefromcasherProudct(index) {
      this.casherproducts.splice(index, 1);
    },
    pushIncasherProudct() {
      this.product = {};
      this.casherproducts.push({});
    },

    async getproducts() {
      var ref = firebase.database().ref("proudcts");
      var vm = this;
      this.isLoading = true;

      await ref.on("value", function(snapshot) {
        //  this.providers=snapshot.val()
        snapshot.forEach(obj => {
          vm.products.push({ ...obj.val() });
        });
        vm.isLoading = false;
      });
    },
    cleardata() {
      this.casherproducts = [{}];
      this.casherResits = {};
      this.printing = false;
    },
    SubmitRisit() {
      this.isLoading = true;

      this.$validator.validate().then(valid => {
        if (valid) {
          let database = firebase.database();
          let cash_resit = database.ref("casherResits").push();
          this.casherResits.id = cash_resit.ref.key;
          this.casherResits.date = moment().format("MMMM Do YYYY, h:mm:ss a");
          this.casherResits.time = moment().format("h:mm:ss a");

          this.casherResits.products = this.casherproducts;
          let vm = this;
          this.casherResits.totalprice = 0;
          this.casherproducts.forEach(element => {
            vm.casherResits.totalprice +=
              parseInt(element.outprice) * parseInt(element.quantity1);
          });

          cash_resit.set(this.casherResits);
          this.isLoading = false;
          this.$buefy.toast.open({
            message: "  ",
            type: "is-success"
          });

          this.printing = true;
        } else {
          this.isLoading = false;

          this.$buefy.toast.open({
            duration: 1000,
            message: `     `,
            position: "is-bottom",
            type: "is-danger"
          });
        }
      });
    }
  },
  mounted() {},
  created() {
    this.getproducts();
  }
};
</script>
<style></style>
